@imgPath: "../img/bootstrap-colorpicker/";

.bgImg(@imgFilename) {
  background-image: url("@{imgPath}@{imgFilename}");
}

.borderRadius(@size) when (@size > 0) {
  border-radius: @size;
}

.colorpicker-saturation {
  float: left;
  width: 100px;
  height: 100px;
  cursor: crosshair;

  .bgImg('saturation.png');
  i {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 5px;
    height: 5px;
    margin: -4px 0 0 -4px;
    border: 1px solid #000;

    .borderRadius(@border-radius-base);
    b {
      display: block;
      width: 5px;
      height: 5px;
      border: 1px solid #fff;

      .borderRadius(@border-radius-base);
    }
  }
}

.colorpicker-hue,
.colorpicker-alpha {
  float: left;
  width: 15px;
  height: 100px;
  margin-bottom: 4px;
  margin-left: 4px;
  cursor: row-resize;
  i {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 1px;
    margin-top: -1px;
    background: #000;
    border-top: 1px solid #fff;
  }
}

.colorpicker-hue {
  .bgImg('hue.png');
}

.colorpicker-alpha {
  display: none;

  .bgImg('alpha.png');
}

.colorpicker-saturation,
.colorpicker-hue,
.colorpicker-alpha {
  background-size: contain;
}

.colorpicker {
  top: 0;
  left: 0;
  z-index: 2500;
  min-width: 130px;
  padding: 4px;
  margin-top: 1px;

  *zoom: 1;
  .borderRadius(@border-radius-base);

  &::before,
  &::after {
    display: table;
    line-height: 0;
    content: "";
  }
  &::before {
    position: absolute;
    top: -7px;
    left: 6px;
    display: inline-block;
    content: "";
    border-right: 7px solid transparent;
    border-bottom: 7px solid #ccc;
    border-bottom-color: rgba(0, 0, 0, .2);
    border-left: 7px solid transparent;
  }
  &::after {
    position: absolute;
    top: -6px;
    left: 7px;
    display: inline-block;
    clear: both;
    content: "";
    border-right: 6px solid transparent;
    border-bottom: 6px solid #fff;
    border-left: 6px solid transparent;
  }
  div {
    position: relative;
  }
  &.colorpicker-with-alpha {
    min-width: 140px;
  }
  &.colorpicker-with-alpha .colorpicker-alpha {
    display: block;
  }
}

.colorpicker-color {
  height: 10px;
  margin-top: 5px;
  clear: both;
  background-position: 0 100%;

  .bgImg('alpha.png');
  div {
    height: 10px;
  }
}

.colorpicker-selectors {
  display: none;
  height: 16px;
  margin-top: 5px;
  clear: both;
  background: none !important;

  i {
    float: left;
    width: 16px;
    height: 16px;
    cursor: pointer;
  }
  i + i {
    margin-left: 3px;
  }
}

.colorpicker-element .input-group-addon,
.colorpicker-element .add-on {
  position: relative;
  z-index: 1;

  i {
    position: relative;
    z-index: initial;
    display: inline-block;
    width: @line-height-computed;
    height: @line-height-computed;
    overflow: hidden;
    vertical-align: text-top;
    cursor: pointer;
    border-radius: @border-radius-base;
    box-shadow: rgba(0, 0, 0, .15) 0 0 0 1px inset;

    &::before {
      position: absolute;
      left: 0;
      z-index: -1;
      width: @line-height-computed;
      height: @line-height-computed;
      content: "";
      background-image: linear-gradient(45deg, #f8f9fa 25%, transparent 25%, transparent 75%, #f8f9fa 75%, #f8f9fa), linear-gradient(45deg, #f8f9fa 25%, transparent 25%, transparent 75%, #f8f9fa 75%, #f8f9fa);
      background-position: 0 0, 30px 30px;
      background-size: 12px 12px;
      box-shadow: rgba(0, 0, 0, .15) 0 0 0 1px inset;
    }
  }
}

.colorpicker.colorpicker-inline {
  position: relative;
  z-index: auto;
  display: inline-block;
  float: none;
}

.colorpicker.colorpicker-horizontal {
  width: 110px;
  min-width: 110px;
  height: auto;

  .colorpicker-saturation {
    margin-bottom: 4px;
  }
  .colorpicker-color {
    width: 100px;
  }
  .colorpicker-hue,
  .colorpicker-alpha {
    float: left;
    width: 100px;
    height: 15px;
    margin-bottom: 4px;
    margin-left: 0;
    cursor: col-resize;
  }
  .colorpicker-hue i,
  .colorpicker-alpha i {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 1px;
    height: 15px;
    margin-top: 0;
    background: #fff;
    border: 0;
  }
  .colorpicker-hue {
    .bgImg('hue-horizontal.png');
  }
  .colorpicker-alpha {
    .bgImg('alpha-horizontal.png');
  }
}

.colorpicker.colorpicker-hidden {
  display: none;
}

.colorpicker,
.colorpicker-inline,
.colorpicker-alpha,
.colorpicker-saturation,
.colorpicker-hue,
.colorpicker-selectors {
  &.colorpicker-visible {
    display: block;
  }
}

.colorpicker-right {
  &::before {
    right: 6px;
    left: auto;
  }
  &::after {
    right: 7px;
    left: auto;
  }
}
